export default ".content-header {\n    text-align: center;\n    padding: 0.5rem 0 2rem;\n}\n\n.content-header__image-wrapper {\n    max-width: 10rem;\n    margin: 0 auto 1rem;\n    height: 11rem;\n    position: relative;\n}\n\n.content-header__image-wrapper > :first-child {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    width: 100%;\n}\n\n.content-header__title {\n    font-size: 1.25rem;\n    font-weight: 200;\n    text-align: center;\n    max-width: 16rem;\n    margin: auto;\n    line-height: 1.4;\n}\n\n.content-header__tag {\n    text-align: center;\n    margin: 0.7rem 0 1rem;\n}\n\n.content-body {\n    padding: 2rem 1.5rem;\n    max-width: 26rem;\n    margin: auto;\n}\n\n.content-body::before {\n    content: '';\n    display: block;\n    position: absolute;\n    left: 0;\n    width: 100%;\n    border-top: 1px solid #eaeced;\n    transform: translateY(-2rem);\n}\n\n.content-body__title {\n    font-size: 1rem;\n    font-weight: 600;\n    text-align: left;\n    margin-top: 0;\n}\n\n.content-body__terms-item,\n.content-body__instructions-item {\n    margin-bottom: 1.5rem;\n}\n\n.content-body__divider {\n    border: none;\n    border-top: 1px solid #eaeced;\n    margin: 2rem 0;\n}\n\n.content-body__instructions-list {\n    /* margin-bottom: 4rem; */\n}\n\n.content-body__instructions-item {\n    list-style: none;\n    display: flex;\n    align-items: center;\n    margin-left: 0;\n}\n\n.content-body__instructions-item > :first-child {\n    flex: 0 0 3rem;\n    margin-right: 1.5rem;\n}\n\n.content-body__instructions-item > :last-child {\n    flex: 1 1 100%;\n    margin: 0;\n}\n";