export default ".tabs {\n    margin: auto;\n    max-width: 400px;\n    padding: 0 18px;\n    white-space: nowrap;\n    font-size: 0;\n    display: flex;\n}\n\n.tab {\n    cursor: pointer;\n    display: block;\n    padding: 30px 0px 15px;\n    font-size: 15px;\n    font-weight: 600;\n    margin-bottom: 0;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    width: 50%;\n    text-align: center;\n    margin-top: 0;\n    white-space: normal;\n    background: transparent;\n    border: none;\n    outline: none;\n    position: relative;\n    transition: color 0.3s;\n}\n\n.tab::after {\n    content: '';\n    display: block;\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    height: 3px;\n    background: #0070ba;\n    transform: scaleX(0);\n    transition: transform 0.2s ease;\n    transform-origin: center right;\n}\n.tab:nth-of-type(2)::after {\n    transform-origin: center left;\n}\n\n.tab:hover {\n    color: #0070ba;\n}\n\n.tab--selected {\n    color: #005187;\n}\n\n.tab--selected::after {\n    transform: scaleX(1);\n}\n\n.tab__transition {\n    position: relative;\n}\n\n.tab__transition-item {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 1;\n    opacity: 0;\n    pointer-events: none;\n    transition: opacity 0.3s;\n    transition-delay: 0.3s;\n}\n\n.tab__transition-item--selected {\n    position: relative;\n    z-index: 2;\n    background: white;\n    opacity: 1;\n    pointer-events: all;\n    transition-delay: 0s;\n}\n\n.calculator {\n    margin-bottom: 10px;\n}\n\n.calculator__finance-terms {\n    position: relative;\n}\n\n.calculator__title {\n    font-size: 1rem;\n    font-weight: 600;\n    text-align: left;\n    margin-top: 0;\n}\n\n.calculator__form {\n    position: relative;\n    display: -ms-flexbox;\n    display: flex;\n    box-sizing: border-box;\n}\n\n.calculator__form::before {\n    content: '$';\n    position: absolute;\n    display: block;\n    z-index: 9;\n    color: black;\n    top: 50%;\n    left: 14px;\n    transform: translateY(-50%);\n}\n\n.calculator__form.calculator__form--loading:before {\n    opacity: 0.5;\n}\n\n.calculator__input {\n    flex: 1 1 70%;\n    -ms-flex: 1 1 70%;\n    border: 1px solid #b7bcbf;\n    border-radius: 5px;\n    background: white;\n    padding: 10px 10px 10px 25px;\n    /*outline: none;*/\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 15px;\n    font-weight: 400;\n    font-style: normal;\n    font-stretch: normal;\n    line-height: 1.6;\n    letter-spacing: normal;\n    color: #2c2e2f;\n    box-sizing: border-box;\n    min-width: 0;\n    margin-right: 1rem;\n}\n\n.calculator__form.calculator__form--loading .calculator__input {\n    opacity: 0.5;\n}\n\n.calculator__input::-webkit-inner-spin-button,\n.calculator__input::-webkit-outer-spin-button {\n    margin: 0;\n    -webkit-appearance: none;\n    -moz-appearance: textfield;\n}\n\n.calculator__input:invalid + .calculator__error-msg {\n    visibility: visible;\n    color: red;\n}\n\n.calculator__input:valid + .calculator__error-msg {\n    visibility: hidden;\n}\n\n.calculator__button {\n    flex: 1 1 20%;\n}\n\n.finance-terms__note {\n    display: block;\n    margin-top: 15px;\n    margin-bottom: 20px;\n    font-size: 13px;\n    font-weight: 400;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    color: #2c2e2f;\n}\n\n.finance-terms__error {\n    font-size: 13px;\n    margin-bottom: 24px;\n}\n\n.finance-terms__table {\n    border-collapse: collapse;\n    width: 100%;\n    position: relative;\n}\n\n.finance-terms__table thead tr {\n    height: 50px;\n    padding: 5px 0 0 5px;\n    background: #f5f5f5;\n    font-weight: 600;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    margin-bottom: 0;\n    font-size: 13px;\n}\n\n.finance-terms__table thead th {\n    padding-top: 5px;\n    text-align: left;\n    padding-left: 8px;\n    vertical-align: top;\n    line-height: 1.38;\n    letter-spacing: -0.2px;\n    color: #2c2e2f;\n}\n\n.finance-terms__table tbody tr:not(:first-of-type) {\n    border-top: solid 0.5px #eaeced;\n    clear: both;\n    width: 99.99%;\n}\n\n.finance-terms__table tbody tr:last-of-type {\n    margin-bottom: 38px;\n}\n\n.finance-terms__table tbody td {\n    padding: 7px 0 7px 5px;\n    box-sizing: border-box;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    margin: 0;\n    padding-left: 8px;\n    font-size: 13px;\n    font-weight: 400;\n    letter-spacing: -0.2px;\n    color: #2c2e2f;\n}\n";