export default "html,\nbody {\n    height: 100% !important;\n    overflow: auto;\n    -webkit-overflow-scrolling: touch;\n}\n\nhtml {\n    min-width: inherit !important;\n}\n\nbody {\n    margin: 0;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-weight: 400;\n    font-size: 16px;\n}\n\nh1,\nh2,\nh3,\nh4,\np,\nli {\n    color: #2c2e2f;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-weight: 400;\n}\n\nh1 {\n    font-size: 30px;\n    margin-bottom: 16px;\n    line-height: 42px;\n    font-family: PayPalSansBig, Helvetica, Arial, Sans-Serif;\n    font-weight: 200;\n}\n\nh2 {\n    font-size: 20px;\n    line-height: 24px;\n    text-align: center;\n    margin-bottom: 20px;\n}\n\nh3 {\n    font-size: 15px;\n    font-weight: 600;\n    margin-bottom: 6px;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n}\n\nsvg {\n    margin: auto;\n    display: block;\n}\n\nh4 {\n    font-size: 15px;\n    line-height: 18px;\n    font-weight: bold;\n}\n\np {\n    font-size: 13px;\n    line-height: 18px;\n}\n\na {\n    text-decoration: none;\n    color: #0070ba;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-weight: 600;\n}\n\nul {\n    padding-left: 0;\n}\n\nli {\n    list-style-type: disc;\n    margin-left: 18px;\n    font-size: 13px;\n    line-height: 18px;\n    letter-spacing: 0;\n}\n\n.button {\n    display: inline-block;\n    background: #0070ba;\n    border: none;\n    color: white;\n    border-radius: 3rem;\n    padding: 0.7em 1.5em;\n    font-size: 1rem;\n    text-align: center;\n    transition: all 0.2s;\n    font-weight: 500;\n    border: 1px solid #0070ba;\n    cursor: pointer;\n}\n\n.button:hover,\n.button:focus {\n    background: #003087;\n    border: 1px solid #003087;\n}\n\n.button:focus {\n    text-decoration: underline;\n    outline: none;\n}\n\n.button--secondary {\n    background: white;\n    color: #0070ba;\n    border: 1px solid #0070ba;\n}\n\n.button--secondary:hover,\n.button--secondary:focus {\n    border-color: #003087;\n    box-shadow: 0 0 0 0.0625em #003087 inset;\n    color: #003087;\n    background: white;\n}\n\n.button--md {\n    font-size: 0.9rem;\n}\n\n.accordion {\n    position: relative;\n}\n\n.accordion__title {\n    margin: 0;\n}\n\n.accordion__description {\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 13px;\n    font-weight: 400;\n    margin: 0 40px 12px 0;\n}\n\n.accordion__content {\n    background-color: white;\n    max-height: 0;\n    overflow: hidden;\n    transition: max-height 0.2s ease-out;\n}\n\n.accordion__symbol {\n    position: absolute;\n    top: 24px;\n    right: 16px;\n    cursor: pointer;\n    pointer-events: none;\n}\n\n.accordion--show .accordion__symbol {\n    transform: rotate(180deg);\n    top: 27px;\n}\n\n.accordion__title-btn {\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: inherit;\n    font-weight: inherit;\n    display: block;\n    width: 100%;\n    border: none;\n    outline-offset: -5px 0;\n    background: transparent;\n    cursor: pointer;\n    margin: 0 0 0.5rem 0;\n    font-weight: 600;\n    padding: 22px 40px 22px 0;\n    text-align: left;\n    -webkit-touch-callout: none;\n    /* iOS Safari */\n    -webkit-user-select: none;\n    /* Safari */\n    -moz-user-select: none;\n    /* Firefox */\n    -ms-user-select: none;\n    /* Internet Explorer/Edge */\n    user-select: none;\n    /* Non-prefixed version, currently\n                    supported by Chrome and Opera */\n}\n\n.accordion__title-btn--with-description {\n    padding-bottom: 11px;\n}\n\n.accordion__divider {\n    border-top: 1px solid #eaeced;\n}\n\n.spinner {\n    position: absolute;\n    left: 50%;\n    top: 50%;\n    border-radius: 50%;\n    border-top: 4px solid #00ff7f;\n    width: 80px;\n    height: 80px;\n    opacity: 0;\n    -webkit-animation: spin 2s linear infinite;\n    animation: spin 2s linear infinite;\n    transition: opacity 0.2s;\n    pointer-events: none;\n    transform-origin: 0 0;\n    z-index: 5;\n}\n\n@-webkit-keyframes spin {\n    0% {\n        -webkit-transform: rotate(0deg) translate(-50%, -50%);\n    }\n    100% {\n        -webkit-transform: rotate(360deg) translate(-50%, -50%);\n    }\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg) translate(-50%, -50%);\n    }\n    100% {\n        transform: rotate(360deg) translate(-50%, -50%);\n    }\n}\n\n.header {\n    transition: 150ms;\n    height: 100%;\n}\n\n.header__logo {\n    box-sizing: border-box;\n    width: 100px;\n    display: block;\n    margin: 0 auto;\n    padding: 18px 0;\n    height: 72px;\n    transition: all 0.3s;\n}\n\n.header__close {\n    display: block;\n    padding: 0;\n    border: none;\n    background: transparent;\n    cursor: pointer;\n    position: absolute;\n    top: 12px;\n    right: 8px;\n}\n\n.header__close svg {\n    height: 48px;\n    width: 48px;\n}\n\n.header__close path {\n    stroke-width: 1px;\n    transition: all 0.3s;\n}\n\n.header__logo-wrapper {\n    transition: all 0.3s;\n    transform: translateX(0) scale(1);\n}\n\n.header__logo-wrapper--shift {\n    transform: translateX(calc(-50% + 35px + 16px)) scale(0.7);\n}\n\n@media (min-width: 26.25rem) {\n    .header__logo-wrapper--shift {\n        transform: translateX(calc(-50% + 50px + 24px)) scale(1);\n    }\n}\n\n.header__apply-now {\n    position: absolute;\n    top: 1.1rem;\n    left: 50%;\n    font-size: 0.9rem;\n}\n\n@media (min-width: 22.5rem) {\n    .header__apply-now {\n        font-size: 1rem;\n    }\n}\n";