export default ".modal__container {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    overflow: hidden;\n}\n\n.modal__container--show .modal__wrapper {\n    transform: translateY(0);\n}\n\n.modal__container--show .modal__overlay {\n    opacity: 1;\n}\n\n.modal__overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: rgba(108, 115, 120, 0.85);\n    transition: opacity 350ms;\n    opacity: 0;\n    z-index: -1;\n}\n\n.modal__overlay-side {\n    position: absolute;\n    top: 0;\n    height: 100%;\n    z-index: 2;\n}\n\n.modal__wrapper {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    transition: transform 350ms;\n    transform: translateY(100%);\n}\n\n.modal__header-wrapper {\n    display: table;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    z-index: 1000;\n}\n\n.modal__header-wrapper::after {\n    content: '';\n    display: table-cell;\n    visibility: hidden;\n    overflow-y: scroll;\n}\n\n.modal__header-container {\n    display: table-cell;\n    width: 100%;\n}\n\n.modal__header {\n    background: rgba(0, 0, 0, 0);\n    text-align: center;\n    width: 100%;\n    max-width: 612px;\n    margin: 0 auto;\n    position: relative;\n    background: white;\n    height: 70px;\n}\n\n.modal__header::after {\n    content: '';\n    display: block;\n    position: absolute;\n    top: 99%;\n    left: 0;\n    width: 100%;\n    height: 40px;\n    background: linear-gradient(rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));\n    pointer-events: none;\n}\n\n.modal__content-wrapper {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    overflow-y: scroll;\n}\n\n.modal__content-background {\n    width: 100%;\n    max-width: 612px;\n    margin: auto;\n    background: white;\n    min-height: 100%;\n}\n\n.modal__content {\n    position: relative;\n    z-index: 50;\n    padding-top: 84px;\n    overflow: hidden;\n}\n\n.modal__main {\n    -webkit-overflow-scrolling: touch;\n    /*overflow-scrolling: touch;*/\n    -moz-overflow-scrolling: touch;\n    position: relative;\n    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0), 0 5px 15px 0 rgba(0, 0, 0, 0);\n    transition: box-shadow 350ms;\n}\n";